
import "@webcomponents/webcomponentsjs/webcomponents-bundle";
import '@webcomponents/webcomponentsjs/custom-elements-es5-adapter.js';
import katex from "katex"


class KatexE extends HTMLElement {
 constructor() {

    super();
   const shadow = this.attachShadow({mode: 'open'});
   const latex = this.getAttribute('latex');
   const display = this.hasAttribute('display');
   const math=katex.renderToString(latex,{displayMode: display}); 
        const fontsize=this.hasAttribute("small") ? "0.925" : "1.2"
    shadow.innerHTML=`<link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/katex@0.12.0/dist/katex.min.css">
    <style> .katex { font-size: ${fontsize}em; }</style>
    ${math}`
 }
}

// Define the new element
customElements.define('katex-e', KatexE);




